import React, {useContext, useState} from 'react';
import {
    Alert, Checkbox,
    CircularProgress,
    FormControl,
    FormControlLabel,
    MenuItem,
    Select,
    TextField,
    Typography
} from "@mui/material";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import {AuthContext} from "../AuthContext";
import {ApiCaller} from "../ApiCaller";
import {useNavigate} from "react-router-dom";
import PageHeader from "../components/PageHeader";

const apiCaller = new ApiCaller()


const NewList = () => {
    const {loginDetails, user} = useContext(AuthContext);
    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");
    const [hasLink, setLink] = useState(false);
    const [hasComment, setComment] = useState(false);
    const [hasCheck, setCheck] = useState(false);
    const [titleError, setTitleError] = useState(false);
    const [loading, setLoading] = useState(false);
    const [apiFail, setApiFail] = useState(false);
    const navigate = useNavigate();



    async function handleCreate() {

        if (title.trim() === "") {
            setTitleError(true);
            return;
        }
        setTitleError(false);


        setLoading(true);
        const info = {
            listName: title.trim(),
            listDesc: desc.trim(),
            type: "MULTI",
            hasComment: hasComment,
            hasLink: hasLink,
            hasCheck: hasCheck
        };
        const newList = {
            listInfo: info,
            owner: user.username,
            editors: [],
            viewers: [],
            items: []
        }
        try {
            const result = await apiCaller.sendPost("list", newList, loginDetails);
            const id = result.listInfo.id;
            navigate(`/web/list/${id}`);

        } catch (e) {
            setApiFail(true)
            setLoading(false)
        }
    }


    return (
        <>
            <Box maxWidth={750} sx={{mx: 'auto'}} variant="contained">

                <PageHeader title={"New List"}></PageHeader>
                <br/>
                <Box>
                    <Box maxWidth={300} sx={{
                        mx: 'auto',
                        mt: 2,
                        p: 2,
                        border: '1px solid ',
                        borderColor: 'primary.darker',
                    }}>
                        <TextField
                            inputProps={{maxLength: 100}}
                            autoComplete="off"
                            id="list-title"
                            label="Title"
                            variant="outlined"
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            fullWidth
                            sx={{marginBottom: 1}}
                            error={titleError}
                            helperText={titleError ? "Title is required" : ""}
                        />
                        <TextField
                            inputProps={{maxLength: 100}}
                            id="list-desc"
                            label="Description (optional)"
                            variant="outlined"
                            value={desc}
                            onChange={(e) => setDesc(e.target.value)}
                            fullWidth
                            sx={{marginBottom: 1}}
                        />

                        <Box sx={{ display: 'flex', flexDirection: 'column', marginBottom: 1 }}>
                            <Typography fontSize={"large"} align={"left"} padding={"8px"} paddingTop={"18px"} color={"secondary.main"}>List functions</Typography>
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={hasCheck}
                                        onChange={(e) => setCheck(e.target.checked)}
                                        color="primary"
                                    />
                                }
                                label="Enable CheckList"
                            />
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={hasLink}
                                        onChange={(e) => setLink(e.target.checked)}
                                        color="primary"
                                    />
                                }
                                label="Enable Links"
                            />
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={hasComment}
                                        onChange={(e) => setComment(e.target.checked)}
                                        color="primary"
                                    />
                                }
                                label="Enable Notes"
                            />
                        </Box>


                        {loading ? (
                            <Box sx={{display: 'flex', justifyContent: 'center', mt: 4}}>
                                <CircularProgress/>
                            </Box>
                        ) : (
                            <Button
                                fullWidth
                                variant="outlined"
                                sx={{
                                    mt: 1,
                                    p: 1
                                }}
                                onClick={handleCreate}
                            >
                                Create
                            </Button>
                        )}

                        {apiFail ? (
                            <Alert severity="error">Failed to create list. Please try again later.</Alert>) : (
                            <Box></Box>)}
                    </Box>
                </Box>
            </Box>
        </>
    );
};

export default NewList;
